import './style.scss';

const ClickableList = ( { items, onClick } ) => {
	return (
		<ul className="gutenbuild-clickable-list">
			{ items.map( ( i ) => {
                const split = i.text.split('.');
                const text = split.pop();

            return(
				<li key={ i.text }>
					<a href="#" onClick={ () => onClick(i) }>
                        {split.map(i=> <span key={i} className="gutenbuild-clickable-list__pill">{i}</span>)}
						<div className="gutenbuild-clickable-list__title">{ text }</div>
					</a>
				</li>
			) } ) }
		</ul>
	);
};

export default ClickableList;
